.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

p {
  margin-bottom: 0;
  font-size: x-large;
  font-weight: 500;
}
span {
  margin-left: 10px;
}
@media (max-width: 1100px) {
  .Header {
    display: contents;
    align-items: center;
  }
  span:first-child {
    margin-left: 0px;
  }
  p{
    margin-bottom: 30px;
  }
}
